import React, {useState} from 'react';
import {ContentState, convertFromHTML, EditorState} from 'draft-js';
import {Editor} from 'react-draft-wysiwyg';
import {stateToHTML} from 'draft-js-export-html';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css'
// import 'node_modules/react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

export const SummerNote = ({label, updateFormValue, updateType, defaultValue}) => {
    const [editorState, setEditorState] = useState(() => {
        if (defaultValue) {
            const blocksFromHTML = convertFromHTML(defaultValue);
            const contentState = ContentState.createFromBlockArray(
                blocksFromHTML.contentBlocks,
                blocksFromHTML.entityMap
            );
            return EditorState.createWithContent(contentState);
        } else {
            return EditorState.createEmpty();
        }
    });

    const onEditorStateChange = (editorState) => {
        setEditorState(editorState);
        const htmlContent = stateToHTML(editorState.getCurrentContent());
        // console.log(htmlContent);
        updateFormValue({updateType, value: htmlContent})
    };

    return (
        <div>
            <p>{label}</p>
            <Editor
                editorState={editorState}
                wrapperStyle={{border: '1px solid #ccc', padding: '10px'}}
                editorStyle={editorStyle}
                onEditorStateChange={onEditorStateChange}
                value={defaultValue || ''}
                initialContentState={defaultValue}
            />

        </div>
    );
};


const editorStyle = {
    border: '1px solid #ccc',
    padding: '10px',
    minHeight: '300px',
};
