import noImage from "../assets/image/No-Image-Placeholder.svg.webp";

export const errorImage = (event) => {
    event.target.src = noImage
}

export function sendToWA(nomor) {
    const template = "Hallo Maparace,";
    return `https://wa.me/${convertWA(nomor)}?text=${template}`;
}

export function convertWA(data) {
    let result = data;
    if (!data || data === "") {
        return "";
    } else {
        // Replace spaces, parentheses, dots, and hyphens
        data = data.replace(/ /g, "").replace(/\(/g, "").replace(/\)/g, "").replace(/\./g, "").replace(/-/g, "").replace(/\+/g, "");

        // Check if the input contains only '+' and digits
        if (!/[^+0-9]/.test(data.trim())) {
            // Check if the input starts with '+62'
            if (data.trim().startsWith("+62")) {
                result = data.trim();
            } else if (data.trim().startsWith("0")) {
                result = "62" + data.trim().substring(2);
            } else if (data.trim().startsWith("8")) {
                result = "628" + data.trim().substring(2);
            } else {
                result = data;
            }
        } else {
            result = "";
        }
    }
    return result;
}

export const statusRegistrationEvent = (data: number) => {
    if (data) {
        return "Telah Diverifikasi"
    } else {
        return "Belum Diverifikasi"
    }
}


export const checkImage = (data: any) => {
    return data ? URL.createObjectURL(data) : noImage
}

export const statusSendWhatsapp = (status: boolean) => {
    if (status) {
        return <small className={'badge badge-success'}>Success</small>
    } else {
        return <small className={'badge badge-error'}>Pending</small>
    }
}