import React, {useEffect, useState} from 'react';
import TitleCard from '../../../components/Cards/TitleCard';
import {useNavigate, useParams} from 'react-router-dom';
import {SummerNote} from '../../../components/Input/SummerNote';
import InputText from '../../../components/Input/InputText';
import SelectBox from '../../../components/Input/SelectBox';
import {DATA_OPTION_PAYMENT_EVENT, DATA_OPTION_STATUS_EVENT} from '../../../utils/dummyData';
import {errorImage} from "../../../utils/Utils";
import {InputFileImage} from '../../../components/Input/InputFileImage';
import {PaperAirplaneIcon} from '@heroicons/react/24/outline'
import {useDispatch} from 'react-redux';
import {
  fetchAddEventAction,
  fetchDetailEventAction,
  fetchUpdateEventAction
} from '../../../app/service/action/eventAction';
import Loading from "../../../components/Loading/Loading";
import {showNotification} from '../../common/headerSlice';

const INITIAL_OBJECT = {
    id: '',
    name: '',
    date: '',
    registration_open_date: '',
    registration_closed_date: '',
    status_event: '',
    note: '',
    payment_status: false,
    fee: '',
    quota: '',
    image_flyer: ''
}


export const AddEvent = () => {
    const {status, id} = useParams()
    console.log(`id`, id);
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [requestParam, setRequestParams] = useState(INITIAL_OBJECT)
    const [showPayment, setShowPayment] = useState(false)
    const [loading, setLoading] = useState(true)
    const updateFormValue = ({updateType, value}) => {
        if (updateType === 'payment_status') {
            setShowPayment(value)
        }
        setRequestParams(prevState => ({...prevState, [updateType]: value}));
    };

    const updateImageValue = ({updateType, value}) => {
        setRequestParams({...requestParam, [updateType]: value})
    }

    useEffect(() => {
        if (id) {
            setLoading(true)
            dispatch(fetchDetailEventAction(id)).then(res => {
                if (res.result) {
                    console.log(`fetching data success...`);
                    setRequestParams({
                        id: res.result.id,
                        name: res.result.name,
                        image_flyer: res.result.image,
                        fee: res.result.price,
                        date: res.result.date,
                        note: res.result.note,
                        registration_open_date: res.result.open,
                        registration_closed_date: res.result.closed,
                        status_event: res.result.status,
                        payment_status: res.result.isPayment,
                        quota: res.result.quota
                    })
                    if (res.result.isPayment === '1') {
                        setShowPayment(true)
                    }
                }
                setLoading(false)
            })
        } else {
            setLoading(false)
            setRequestParams(INITIAL_OBJECT)
        }
    }, [dispatch, id]);


    const handleSubmit = () => {
        setLoading(true)
        if (id) {
            console.log('update');
            dispatch(fetchUpdateEventAction(requestParam)).then(res => {
                if (res.result) {
                    dispatch(showNotification({message: "Berhasil update event", status: 1}))
                    navigate(`/app/edit-event/${status}/${id}`, {replace: true})
                }
                setLoading(false)
            })
        } else {
            dispatch(fetchAddEventAction(requestParam, status)).then(res => {
                if (res.result) {
                    dispatch(showNotification({message: "Berhasil tambah event", status: 1}))
                    navigate(`/app/event/${status}`, {replace: true})
                }
                setLoading(false)
            })
        }
    }

    return (
        <>
            {loading ? <Loading/> :
                <TitleCard title={`Add Event ${status}`} topMargin={'mt-2'}>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <InputText labelTitle="Nama Event" updateType={'name'}
                                   defaultValue={requestParam.name}
                                   updateFormValue={updateFormValue}/>
                        <InputText type='datetime' labelTitle="Tanggal Event" updateType={'date'}
                                   defaultValue={requestParam.date}
                                   updateFormValue={updateFormValue}/>
                    </div>
                    <div className="mt-4 md:grid-cols-2 gap-6">
                        <SummerNote label={'Keterangan'} updateFormValue={updateFormValue} updateType={'note'}
                                    defaultValue={requestParam.note}/>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                        <InputText type='datetime-local' labelTitle="Tanggal Registrasi"
                                   updateType={'registration_open_date'}
                                   defaultValue={requestParam.registration_open_date}
                                   updateFormValue={updateFormValue}/>
                        <InputText type='datetime-local' labelTitle="Tanggal Registrasi Tutup"
                                   updateType={'registration_closed_date'}
                                   defaultValue={requestParam.registration_closed_date}
                                   updateFormValue={updateFormValue}/>
                        <SelectBox labelTitle='Status Event' options={DATA_OPTION_STATUS_EVENT}
                                   updateType={'status_event'}
                                   defaultValue={requestParam.status_event}
                                   updateFormValue={updateFormValue}/>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                        <SelectBox labelTitle='Berbayar' options={DATA_OPTION_PAYMENT_EVENT}
                                   updateType={'payment_status'}
                                   defaultValue={requestParam.payment_status}
                                   updateFormValue={updateFormValue}/>
                        {
                            showPayment &&
                            <InputText type={'number'} labelTitle="Fee Pendaftaran" updateType={'fee'}
                                       defaultValue={requestParam.fee}
                                       updateFormValue={updateFormValue}/>
                        }
                        <InputText type={'number'} labelTitle="Kuota Peserta" updateType={'quota'}
                                   defaultValue={requestParam.quota}
                                   updateFormValue={updateFormValue}/>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div className={'image'}>
                            <img src={requestParam.image_flyer} alt="" onError={errorImage}/>
                        </div>
                        <InputFileImage labelTitle={'Image Flyer'} updateType={'image_flyer'}
                                        updateFormValue={updateImageValue}/>
                    </div>
                    <div className={'button'}>
                        <button className={'btn btn-primary'} onClick={() => handleSubmit()}>
                            <PaperAirplaneIcon className='h-6 w-6'/>SIMPAN
                        </button>
                    </div>
                </TitleCard>
            }
        </>
    );
};
