import React, {useState} from "react";
import Compressor from 'compressorjs';

export const InputFileImage = ({
                                   labelTitle,
                                   labelStyle,
                                   defaultValue = '',
                                   type = 'file',
                                   updateFormValue,
                                   updateType,
                                   containerStyle
                               }) => {

    const [value] = useState(defaultValue)
    const [, setCompressedFile] = useState();
    const [, setCompressedSize] = useState();

    const handleChange = (e) => {
        const file = e.target.files[0];
        const reader = new FileReader();
        const compressFile = (fileToCompress) => {
            new Compressor(fileToCompress, {
                quality: 0.5, // 0.6 can also be used, but it's not recommended to go below.
                success: (compressedResult) => {
                    setCompressedFile(compressedResult);

                    // Calculate the size in megabytes
                    const sizeInMB = compressedResult.size / 1048576; // 1 MB = 1024 * 1024 bytes
                    setCompressedSize(sizeInMB);

                    reader.onload = () => {
                        updateFormValue({
                            updateType,
                            value: compressedResult
                        });
                    };

                    reader.readAsDataURL(compressedResult);
                },
                error: (err) => {
                    console.error('Compression failed:', err);
                },
            });
        };

        if (file.size > 2 * 1048576) { // Check if file size is greater than 2 MB
            compressFile(file);
        } else {
            setCompressedFile(file);

            // Calculate the size in megabytes
            const sizeInMB = file.size / 1048576;
            setCompressedSize(sizeInMB);

            reader.onload = () => {
                updateFormValue({
                    updateType,
                    value: file
                });
            };

            reader.readAsDataURL(file);
        }
    };

    return (
        <div className={`form-control w-full ${containerStyle}`}>
            <label className="label">
                <span className={"label-text text-base-content " + labelStyle}>{labelTitle}</span>
            </label>
            <input className="file-input file-input-bordered file-input-warning w-full" type={type} accept={'image/*'}
                   placeholder={value || defaultValue}
                   onChange={(e) => handleChange(e)}/>
        </div>
    )
}
